/**
 * @memberof PSPDFKit
 * @example
 *   try {
 *     PSPDFKit.someFunctionThatFails()
 *   } catch (error) {
 *     error instanceof PSPDFKit.Error // => true
 *     error.message // Useful error message
 *   }
 *
 * @class Error
 * @extends Error
 * @classdesc A PSPDFKit.Error indicates a problem with PSPDFKit. It is a subclass of and behaves like a
 * regular JavaScript error.
 * @public
 * @summary PSPDFKit related error.
 */
const PSPDFKitError = function PSPDFKitError(messageOrError?: string | Error) {
  let error

  if (messageOrError instanceof Error) {
    error = messageOrError
  } else {
    error = new Error(messageOrError)
  }

  Object.setPrototypeOf(error, PSPDFKitError.prototype)

  return error
  // this is a hack because PSPDFKitError is a function that returns a value so we can't use new with it
} as any

// Use some prototype magic in order to make `new PSPDFKitError() instanceof PSPDFKitError`
PSPDFKitError.prototype = Object.create(Error.prototype, {
  name: {
    value: 'PSPDFKitError',
    enumerable: false,
  },
})

export default PSPDFKitError

/** Creates an error function that creates a {@link PSPDFKitError} with given message prefix. */
export function createError(prefix = ''): (message: string) => Error {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  return (message: string) => new PSPDFKitError(prefix === '' ? message : `${prefix}: ${message}`)
}
