  async parseXFDF(xfdf: string): Promise<IConvertedXFDFJSON> {
    function str2ab(str) {
      const buf = new ArrayBuffer(str.length * 2) // 2 bytes for each char
      const bufView = new Uint16Array(buf)

      for (let i = 0, strLen = str.length; i < strLen; i++) {
        bufView[i] = str.charCodeAt(i)
      }

      return buf
    }

    function ab2str(buffer: ArrayBuffer) {
      let returnString = ''
      const bytes = new Uint8Array(buffer)
      const len = bytes.byteLength

      for (let i = 0; i < len; i++) {
        returnString += String.fromCharCode(bytes[i])
      }

      return returnString
    }

    const vector = new _module.VectorUint8()

    try {
      const uint = new Uint8Array(str2ab(xfdf))

      for (let i = 0; i < uint.byteLength; i++) {
        vector.push_back(uint[i])
      }

      const result: BinaryReply = await _module.dispatchCommandWithBinary(
        JSON.stringify({ type: 'convert_xfdf_to_instant_json' }),
        vector,
      )

      if (result.hasError()) {
        const error = new Error(
          result.getErrorMessage() ||
            'There was an error while executing the command: convert_xfdf_to_instant_json',
        )

        // JavaScript code must explicitly delete any C++ object handles it has received, or the
        // Emscripten heap will grow indefinitely.
        //
        // https://kripken.github.io/emscripten-site/docs/porting/connecting_cpp_and_javascript/embind.html#memory-management
        result.delete()
        throw error
      }

      const replies: Record<string, unknown> | Uint8Array[] = []

      for (let i = 0; i < result.getRepliesCount(); i++) {
        if (result.hasBinaryReply(i)) {
          replies.push(result.getBinaryReply(i).slice(0))
        }
      }

      const instantJSONString = ab2str(replies[0].buffer)

      result.delete()

      return JSON.parse(instantJSONString)
    } catch (e) {
      throw e
    }
  }

