  async importInstantJSON(instantJSON: InstantJSON): Promise<void> {
    exec('importInstantDocumentJSON', JSON.stringify(instantJSON))
  }

  async exportInstantJSON(): Promise<InstantJSON> {
    const previousFormJSONUpdateBatchMode = formJSONUpdateBatchMode

    if (formJSONUpdateBatchMode) {
      setFormJSONUpdateBatchMode(false)
    }

    const result = exec('exportInstantDocumentJSON')

    if (previousFormJSONUpdateBatchMode) {
      setFormJSONUpdateBatchMode(true)
    }

    return JSON.parse(result.InstantDocumentJSON)
  }

