function imageFormatForImageDimensions(
  format: ReturnImageFormat,
  {
    width,
    height,
  }: {
    width: number
    height: number
  },
) {
  if (format === 'webp' && (width > MAX_WEBP_DIMENSION || height > MAX_WEBP_DIMENSION)) {
    return 'png'
  }

  return format
}

function flattenSnappingPoints(
  snappingPoints: MeasurementSnappingPointsResponse,
): [x: number, y: number][] {
  const allSnappingPoints = [
    ...snappingPoints.endpoints,
    ...snappingPoints.intersections,
    ...snappingPoints.midpoints,
  ]

  const points: [x: number, y: number][] = []

  for (let i = 0; i < allSnappingPoints.length; i = i + 2) {
    const coordinate: [number, number] = [allSnappingPoints[i], allSnappingPoints[i + 1]]
    points.push(coordinate)
  }

  return points.sort((prev, curr) => (prev[0] == curr[0] ? prev[1] - curr[1] : prev[0] - curr[0]))
}
