export const TEXT_MAX_LENGTH = 32768

export const INITIAL_TEXT_TEXTAREA_HEIGHT = 18 //px

export const INITIAL_TEXT_TEXTAREA_WIDTH = 50 //px

/**
 * The breakpoint from which on we show the note popover as a modal instead of the full screen view.
 */
export const NOTE_FULLSCREEN_BREAKPOINT = BREAKPOINT_XS

/**
 * The breakpoint from which on we show the comments as a overlay instead of a sidebar.
 */
export const COMMENTS_FULLSCREEN_BREAKPOINT = BREAKPOINT_XS

/**
 * The comment threads sidebar width.
 */
export const COMMENTS_SIDEBAR_WIDTH = 320

/**
 * To make less request when the user types in a search request, we debounce it.
 */
export const SEARCH_DEBOUNCE_TIME = 250 //ms

/**
 * Default value for the minDefaultZoomLevel Configuration option.
 */
export const DEFAULT_MIN_DEFAULT_ZOOM_LEVEL = 0.5

/**
 * Default value for the maxDefaultZoomLevel Configuration option.
 */
export const DEFAULT_MAX_DEFAULT_ZOOM_LEVEL = 10

