export const DEFAULT_SPREAD_SPACING = 20 //px

export const DEFAULT_PAGE_SPACING = 0 //px

export const DEFAULT_VIEWPORT_PADDING = 20 //px

/**
 * The maximum distance a `mousedown` can be apart from a `mouseup` to be still considered a click
 * and not another interaction.
 */
export const MOUSE_CLICK_MAX_DISTANCE = 5 //px

/**
 * Inset border width of the selection resize anchor in px.
 */
export const RESIZE_ANCHOR_STROKE_WIDTH = 1

/**
 * For native builds (i.e. UWP) performance is greatly improved with a larger tile size.
 * See this issue for some more detail: https://github.com/PSPDFKit/PSPDFKit-Web/issues/2858
 */
export const TILE_SIZE = PSPDFKIT_DEFINES.STANDALONE_NATIVE ? 2048 : 512 //px

export const TILE_OVERLAPPING = 5 //px

export const ZOOM_STEP = 1.25 // zoomLevel *= 125%

/**
 * Class name of the root node where we mount React inside frameWindow
 */
export const ROOT_NODE_CLASS_NAME = 'PSPDFKit-Root'

/**
 * The size of an svg icon for a Note annotation in PDF page coordinates.
 */
export const NOTE_ANNOTATION_ICON_SIZE = 32 //px

/**
 * The size of an svg icon for a Comment Marker annotation in PDF page coordinates.
 */
export const COMMENT_MARKER_ANNOTATION_ICON_SIZE = 32 //px

/**
 * The amount of spreads to pre render the overviews for around the current spread in the viewport.
 */
export const SPREADS_OVERVIEWS_TO_PRE_RENDER = 5

/**
 * The number of reusable workers for standalone.
 */
export const STANDALONE_WORKERS_POOL_LIMIT = 1

/**
 * For mouse events, the main and secondary button are referenced using an integer value.
 */
export const MAIN_MOUSE_BUTTON = 0

export const SECONDARY_MOUSE_BUTTON = 2

// MouseEvent.buttons identifies the main mouse as 1 instead of 0 (which is used for MouseEvent.button)
// For mousemove events, we have to use MouseEvent.buttons instead of MouseEvent.button
export const MAIN_MOUSE_BUTTON_BEING_PRESSED = 1

/**
 * Values required for printing. Check out utils/print/dom.js
 */
export const CSS_UNITS = 96.0 / 72.0

export const LOW_PRINT_RESOLUTION = 150

export const HIGH_PRINT_RESOLUTION = 300

export const LOW_RESOLUTION_PRINT_UNITS = LOW_PRINT_RESOLUTION / 72.0

export const HIGH_RESOLUTION_PRINT_UNITS = HIGH_PRINT_RESOLUTION / 72.0

export const RENDER_TILE_PAGE_MAX_SIZE = 5000

/**
 * Maximum image annotation proportion relative to the page size.
 */
export const IMAGE_ANNOTATION_MAX_PAGE_SIZE_PROPORTION = 2 / 3

/**
 * A4 pixel sizes in 72dpi resolution.
 */
export const A4_WIDTH = 595

export const A4_HEIGHT = 842

/**
 * Percentage of white applied to a color to obtain a background color
 * for custom stamps editor.
 * Same as used in Core for custom stamp annotations.
 */
export const LIGHTER_BACKGROUND_PERCENTAGE = 50

/**
 * Initial custom stamp aspect ratio (width / height)
 * The initial, empty stamp has a width of
 * CUSTOM_TEMPLATE_HEIGHT * CUSTOM_TEMPLATE_INITIAL_ASPECT_RATIO,
 * which is used as minimum width for edited custom stamps so as to make sure
 * `subtitle` always fits.
 */
export const CUSTOM_TEMPLATE_INITIAL_ASPECT_RATIO = 2

/**
 * Custom stamp editor preview height
 */
export const CUSTOM_TEMPLATE_HEIGHT = 96

/**
 * Initial custom stamp editor preview width
 */
export const DEFAULT_STAMP_WIDTH = CUSTOM_TEMPLATE_INITIAL_ASPECT_RATIO * CUSTOM_TEMPLATE_HEIGHT

/**
 * Time to wait before the loading indicator is shown while rendering (image and stamp annotations).
 */
export const LOADING_INDICATOR_DELAY = 300 // ms

/**
 * Time to wait before notifying an annotation update with the last updated value (for debounced notifications).
 */
export const UPDATE_ANNOTATION_WAIT = 1000 // ms

/* Base arc radius for cloudy borders, to be multiplied by the annotation's
 * border effect intensity value.
 */
export const CLOUD_BORDER_EFFECT_BASE_RADIUS = 4.25

/**
 * Cloudy border intensity supported by the UI toolbox.
 */
export const UI_CLOUDY_BORDER_INTENSITY = 2

export const MAX_RESOURCE_REQUESTS_IN_QUEUE_SERVER = /*#__PURE__*/ 10

/**
 * Maximum value for WebP images width or height (14 bits).
 */
export const MAX_WEBP_DIMENSION = /*#__PURE__*/ 16383
