/**
 * Wraps the onload and onerror events from FileReader as a promise for easier usage.
 */
export function readBlobData(inputFile: Blob): Promise<Uint8Array> {
  const temporaryFileReader = new FileReader()

  return new Promise((resolve, reject) => {
    temporaryFileReader.onerror = error => {
      // @ts-expect-error
      reject(new Error(error))
    }

    temporaryFileReader.onload = event => {
      resolve(new Uint8Array(event.target?.result as ArrayBufferLike))
    }

    temporaryFileReader.readAsArrayBuffer(inputFile)
  })
}
