import 'isomorphic-fetch'

/**
 * Loading `babel-polyfill` will create a global variable with the name `_babelPolyfill` to prevent
 * the polyfills from being injected twice. The following lines prevents errors from being thrown
 * when `babel-polyfill` is already loaded by skipping it entirely.
 *
 * We still inject our version of babel-polyfill since all of the `core-js` polyfills will do a
 * proper feature test first - previous implementations will only be overwritten when they are not
 * already present. This should prevent problems where the customer has an older version of
 * `babel-polyfill` loaded which might miss important features.
 */
if (typeof window == 'object') {
  ;(<any>window)._babelPolyfill = false
}

import 'core-js/stable'
import 'regenerator-runtime/runtime'
